#attendance {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 100%;
  padding: 3rem;
  min-height: 100vh;
}

.attendance-heading span span {
  font-size: 1.7rem;
  font-weight: 400;
}

#attendance-table-id {
  padding: 2rem 0;
}

.attendance-table-div {
  width: 100%;
  height: auto;
  background-color: var(--color-white);
  border-radius: 1rem;
  box-shadow: 0px 2px 6px 0px rgba(13, 10, 44, 0.08);
}

/* Tabspame */

.tabpane-span {
  padding: 1rem 2rem;
  font-size: 0.875rem;

  text-align: center;

  font-family: 'Open' Sans;
  font-size: 0.875rem;
  font-style: normal;
  font-weight: 700;
  line-height: 1.25rem; /* 142.857% */
}

.ant-tabs-ink-bar {
  background-color: var(
    --color-purple
  ) !important; /* Change this to the desired color */
}

