#teacherprofile {
  position: relative;
  width: 4rem;
  height: 4rem;
}
#teacherprofile img {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

/* Large desktop screens (1440px and above) */
@media (min-width: 1440px) {
}

/* Small desktop screens and tablets (1024px to 1279px) */
@media (min-width: 1024px) and (max-width: 1279px) {
}

/* Tablets (768px to 1023px) */
@media (min-width: 768px) and (max-width: 1023px) {
  #user-profile-div {
    display: none;
  }
}

/* Mobile devices in landscape orientation (480px to 767px) */
@media (min-width: 480px) and (max-width: 767px) {
  #user-profile-div {
    display: none;
  }
}

/* Mobile devices in portrait orientation (320px to 479px) */
@media (min-width: 320px) and (max-width: 479px) {
  #user-profile-div {
    display: none;
  }
}
